import React from 'react';
import '../style/product.css'
export default class Product extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            score:''
        }
    }
    render(){
        return(
            <div className='product'>
                <img style={{height: '100%', width: '100vw'} } src='http://new.szdtb.com/image/company.jpg' />
                <p className='companyDec'></p>
                <p className='companyDec'></p>
                <h3 style={{textAlign:'center'}}></h3>
                <p className='companyDec'>
                    
                </p>
                <img style={{height: '100%', width: '100vw'} } src='http://new.szdtb.com/image/equipment.png' />
                <p className='companyDec'></p>
                
                <div className='scan'>
                    <img style={{height: '100%', width: '100%'} } src='http://new.szdtb.com/image/scan.png' />
                </div>
                <p style={{textAlign:'center'}}> D24-D30</p>
                <img style={{height: '100%', width: '100vw'} } src='http://new.szdtb.com/image/display1.jpg' />
                <div style={{ width: '97vw',margin:'0.5vh auto 0'} }>
                    <img style={{height: '100%',display:'block', width: '100%'} } src='http://new.szdtb.com/image/display2.png' />
                </div>
                
            </div>
        );
    }
}


